import useSWR from '@/hooks/useSWR';
import request from '@/utils/request';
import { stringify } from 'qs';
import { requestBasePath } from '@/services/login';

// 新增评分 /saveMovieGrade
export function addGrade(params: any) {
  return request(`${ requestBasePath }/comment/saveMovieGrade`, {
    method: 'POST',
    body: params
  });
}

// 发布评论 /insertComment
export function punlishCommont(params: any) {
  return request(`${ requestBasePath }/comment/insertComment`, {
    method: 'POST',
    body: params
  });
}

// 更新评论 /updateComment
export function updateCommont(params: any) {
  return request(`${ requestBasePath }/comment/updateComment`, {
    method: 'POST',
    body: params
  });
}

